.kakao-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border: 1px solid #42adfb;
  border-radius: 10px;
  width: 40%;
}
.kakao-box > p {
  font-size: 12px;
  color: #42adfb;
}

.kakao-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
